.container {
    max-width: 1200px; /* Максимальная ширина контейнера */
   /* margin: 0 auto;  */
    padding-top: 20px; 
    padding-left: 60px;
    padding-right: 60px;
    padding-bottom: 60px;  /* Горизонтальные отступы */
}


.nav{
    display: flex;
    align-items: center;
}

.nav-list{
    display: flex;
    gap: 60px;
}

.nav-list{
    margin-left: 222px;
    margin-right: 124px;
}

.nav-btn{
    border: 1px solid;
    border-color: azure;
    padding: 16px 31px;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.7;
    cursor: pointer;
}

.hero-section {
    display: flex;
    padding-top:209px;
}
.hero-text-container {
    display: flex;
    gap: 8px;
    align-items: center;
    font-size: 16px;
}
.hero-head {
margin-top: 16px;
font-size: 72px;
font-weight: 400;
width: 656px;

}
.hero-overview {
margin-top: 43px;
font-size: 18px;
font-weight: 400;
width: 615px;

}
.hero-blok-btn{
    display: flex;
    gap: 40px;
    margin-top: 53px;
    align-items: center;
}

.hero-btn-explore{
    color: rgba(255, 255, 255, 1);
    background-color: 
    rgba(87, 184, 255, 1);
    padding: 21px 34px;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.7;
    cursor: pointer;
}
